@import url('https://fonts.googleapis.com/css2?family=Allerta+Stencil&family=Dekko&family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800;900;1000&family=Righteous&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Dekko', cursive;
}

a{
  color: black;
  font-weight: 600;
}

#del_btn{
  font-weight: 600;
  cursor: pointer;
}
*{
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
}

.container{
  margin-left: 3rem;
}

.card{
  margin: 5% 0%;
  display: grid;
}

.card-body{
  margin: 0% 0% 0% 3%;
  padding: 6% 0%;
}

.container{
  display: flex;
}

.img_f{
  width: 300px;
}

.card-deck{
  border: 1px solid black;
  border-bottom: 1rem;
  color: white;
}

.btn_del{
  color: black;
  font-weight: 600;
}