@import url('https://fonts.googleapis.com/css2?family=Allerta+Stencil&family=Dekko&family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800;900;1000&family=Righteous&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Mulish';
}

#title{
  font-size: 1.3rem;
  font-weight: 600;
  margin-left: 0.8rem;
  transition: 0.3s ease-in-out;
}

#title:hover{
  color: #17cf97;
}

#logo_link{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #17cf97;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  /* flex-grow: 1; */
}

nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #1b2430;
  padding: 10px 60px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.06);
}

#navLink{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.nav-item{
  line-height: 40px;
}



.nav-item.active {
  color: #17cf97;
  border: 1px solid #17cf97;
}


#navLink li a{
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  transition: 0.8s ease-in-out;
}

#navLink li a:hover{
  color: #17cf97;
}

#mobile i{
  color: white;
}

.active{
  color: #17cf97;
  border: 1px solid #17cf97;
}

.nav-links{
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}

.nav-icon{
  display: none;
}

.nav-menu{
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

#user_box{
  display: flex;
  align-items: center;
  margin-left: -4rem;
  color: #17cf97;
  font-size: 1em;
  font-weight: 600;
  text-transform: uppercase;
}

#image_u{
  width: 40px;
  margin-top: 0px;
  height: 40px;
  border-radius: 5px;
}

#wel{
  margin-left: 0.5rem;
}

@media screen and (max-width: 960px) {
  
  #logo_link{
    margin-left: -45px;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1px solid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #1f5156;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  .nav-item .active {
    color: #17cf97;
    border: none;
  }
  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #17cf97;
  }

  #user_box{
    margin-left: 1.2rem;
  }

  #wel{
    margin-left: 0.5rem;
  }
}