@import url('https://fonts.googleapis.com/css2?family=Allerta+Stencil&family=Dekko&family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800;900;1000&family=Righteous&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Dekko', cursive;
}

table{
  margin-top: 15px;
  margin: 15px;
  border: none;
}

Table td{
  text-align: center;
  font-size: 20px;
  margin: 10px;
  background-color: black;
  color: white;
}

button{
  margin: 10px;
  padding: 5px 20px;
}

.table-bordered{
  width: 83.4rem;
}


@media screen and (max-width: 768px) {
  
  .table-bordered{
    width: 20.4rem;
  }
}