*{
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  text-decoration: none;
}

.container{
  margin-left: 3rem;
}

.card{
  margin: 5% 0%;
  display: grid;
}

.card-body{
  margin: 0% 0% 0% 3%;
  padding: 6% 0%;
}

.container{
  display: flex;
}

.img_f{
  width: 300px;
}

.card-deck{
  border: 1px solid black;
  border-bottom: 1rem;
  color: white;
}

.btn_del{
  color: black;
  font-weight: 600;
}